//–∫–ª–∞—Å—Å Section, –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–≤–µ—á–∞–µ—Ç –∑–∞ –æ—Ç—Ä–∏—Å–æ–≤–∫—É —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ

export default class Section {
    constructor({ renderer }, container) {
        this._renderer = renderer;
        this._container = container;

    };

    renderCards(items) {
        items.reverse().forEach(item => {//–ø–µ—Ä–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç –º–∞—Å—Å–∏–≤–∞ —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è –ø–æ—Å–ª–µ–¥–Ω–∏–º, –∞ –ø–æ—Å–ª–µ–¥–Ω–∏–π ‚Äî –ø–µ—Ä–≤—ã–º.
            this._renderer(item);
        });
    };

    addItem(item) {
        console.log(this._container);
        this._container.prepend(item);
    };
};