import './index.css';
import Card from '../scripts/Card.js';
import Api from '../scripts/Api.js';
import Section from '../scripts/Section.js';
import { FormValidator, validationConfig } from '../scripts/FormValidator.js';
import {
  elements,
  userAvatarBtn,
  userEditBtn,
  userAddBtn,
  popupAvatar,
  popupEdit,
  popupAdd,
  popupImage,
  popupConfirm,
  inputName,
  inputAbout,
  userAvatar,
  userName,
  userAbout
} from '../utils/constants.js';
import PopupWithForm from '../scripts/PopupWithForm.js';
import PopupWithImage from '../scripts/PopupWithImage.js';
import PopupWithConfirm from '../scripts/PopupWithConfirm.js';
import UserInfo from '../scripts/UserInfo.js';

export const apiConfig = {
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-19',
  // userId: '486f4cfdd86508efc4a3eb06',// —Å–µ—Ä–≤–µ—Ä –Ω–µ —Ä–∞–±–æ—Ç–∞–ª. —É–±—Ä–∞–Ω–∞ –ø—Ä–∏–≤—è–∑–∫–∞ –ø–æ id
  headers: {
    authorization: '1d464b43-9695-4331-8a3a-49b913826759',
    'Content-Type': 'application/json'
  }
}

export const profileConfig = {
  name: userName,
  about: userAbout,
  avatar: userAvatar
}

let myId;//–ø–æ–¥–≥—Ä—É–∂–∞–µ–º —Å —Å–µ—Ä–≤–µ—Ä–∞ –≤ 50-–π —Å—Ç—Ä–æ–∫–µ

const api = new Api(apiConfig);
const user = new UserInfo(profileConfig);

Promise.all([api.getUserData(),
api.getInitialCards()])
  .then(([userData, result]) => {
    myId = userData._id
    user.setUserInfo(userData);
    cardList.renderCards(result);
  })
  .catch((err) => console.log(err));


//–≤–∞–ª–∏–¥–∞—Ü–∏—è
const popupFormEditValidator = new FormValidator(popupEdit, validationConfig);
popupFormEditValidator.enableValidation();
const popupFormAddValidator = new FormValidator(popupAdd, validationConfig);
popupFormAddValidator.enableValidation();
const popupFormAvatarValidator = new FormValidator(popupAvatar, validationConfig);
popupFormAvatarValidator.enableValidation();

const popupWithConfirm = new PopupWithConfirm(popupConfirm);
const popupWithImage = new PopupWithImage(popupImage);

// –æ—Ç—Ä–∏—Å–æ–≤–∫–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤.. –º–∞—Å—Å–∏–≤

function renderCards(item) {//–æ—Ç–¥–µ–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞. –º–æ–∂–Ω–æ –Ω–∞—Å—Ç—Ä–æ–∏—Ç—å –ø–æ—Ä—è–¥–æ–∫ –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
  const card = createCard(item)
  cardList.addItem(card);
};

function createCard(item) {//–æ—Ç–¥–µ–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏. –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≥–æ—Ç–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
  const card = new Card(item, "#element-template", myId, {
    handleCardClick: () => {
      popupWithImage.open(item.name, item.link);
    },
    handleConfirmClick: () => {
      popupWithConfirm.open();
      popupWithConfirm.—ÅonfirmHandler(() => {
        api
          .deleteCard(card._id)
          .then(() => {
            card.removeCard();
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
    handleLikeClick: () => {
      api
        .likeCard(item._id)
        .then((item) => {
          card.likeCounter(item.likes);
          card.likeThatCard();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleDislike: () => {
      api
        .dislikeCard(item._id)
        .then((item) => {
          card.likeCounter(item.likes);
          card.likeThatCard();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  });
  return card.generateCard();
};



const cardList = new Section({ renderer: (item) => renderCards(item) }, elements);

//—Å–º–µ–Ω–∞ –∏–º–µ–Ω–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
const popupEditProfile = new PopupWithForm(popupEdit, {
  handlerFormSubmit: (item) => {
    popupEditProfile.loadingProgress('–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...')
    api
      .setUserData(item)
      .then((result) => {
        user.setUserInfo(result);
        popupEditProfile.close();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        popupEditProfile.loadingProgress('–°–æ—Ö—Ä–∞–Ω–∏—Ç—å')
      })
  },
});

//–¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏
const popupAddCard = new PopupWithForm(popupAdd, {
  handlerFormSubmit: (item) => {
    popupAddCard.loadingProgress('–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...')
    api
      .createCard(item)
      .then((item) => {
        renderCards(item);
        popupAddCard.close();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        popupAddCard.loadingProgress('–°–æ–∑–¥–∞—Ç—å')
      })
  },
});

//—Å–º–µ–Ω–∞ –∞–≤–∞—Ç–∞—Ä–∫–∏
const popupChangeAvatar = new PopupWithForm(popupAvatar, {
  handlerFormSubmit: (item) => {
    popupChangeAvatar.loadingProgress('–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...')
    api
      .changeAvatar(item)
      .then((data) => {
        user.setUserAvatar(data);
        popupChangeAvatar.close();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        popupChangeAvatar.loadingProgress('–°–æ—Ö—Ä–∞–Ω–∏—Ç—å')
      })
  },
});

userEditBtn.addEventListener("click", () => {
  const bioUser = user.getUserInfo();
  inputName.value = bioUser.name;
  inputAbout.value = bioUser.about;

  popupEditProfile.open();
  popupFormEditValidator.resetValidationState();
});

userAddBtn.addEventListener("click", () => {
  popupAddCard.open();
  popupFormAddValidator.resetValidationState();
});

userAvatarBtn.addEventListener("click", () => {
  popupChangeAvatar.open();
  popupFormAvatarValidator.resetValidationState();
});

popupChangeAvatar.setEventListeners();
popupEditProfile.setEventListeners();
popupAddCard.setEventListeners();
popupWithImage.setEventListeners();
popupWithConfirm.setEventListeners();